.image {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-height: 100%;

  &.hidden {
    display: none;
  }
}

.image--extra-small {
  max-width: 80px;
  max-height: 60px;
}

.image--small {
  max-width: 160px;
  max-height: 120px;
}

.image--medium {
  max-width: 240px;
  max-height: 180px;
}

.image--large {
  max-width: 360px;
  max-height: 270px;
}

.image--extra-large {
  max-width: 640px;
  max-height: 480px;
}

.image--border {
  border: $border--medium $color--line-system;
}

.image--logo {
  max-width: 215px;
  max-height: 46px;
  padding: 0;
}

.image--round {
  border-radius: 100%;
  width: 125px;
  height: 125px;
}
